/* hey! */
body {
  font-family: "Cabinet Grotesk", sans-serif;
}

.my_container {
  padding-left: 128px;
  padding-right: 128px;
}
.more_bg_gradient {
  background: linear-gradient(239.58deg, #c76ec4 -14%, #396ce8 114.09%);
  background-position: center;
  background-size: 100% auto;
  background-repeat: no-repeat;
  width: 130px;
  min-width: 130px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
}
/* header, nav */
.brief--download--figma_file:hover {
  @apply bg-primary text-clr_white_1;
}
.brief--download--figma_file:hover img {
  @apply invert;
}
/* brief section */
.top_content {
  width: 100%;
  height: 100%;
  background-image: url(./assets/bg_grad_blue.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1;
}
.brief--download--handle_dropdown:hover ul {
  visibility: visible;
}
.brief--download--handle_dropdown:hover .downloadBtn--arrow_div {
  @apply bg-clr_green_red;
}
.brief--download--figma_file {
  @apply border-2 border-primary flex items-center justify-center rounded-lg mx-6 h-14 px-5;
}
.download--versions_drop {
  @apply absolute invisible flex flex-col bg-clr_white_1 p-2 w-full rounded-lg;
  box-shadow: 0px 2px 4px rgba(139, 151, 180, 0.32);
}

/* search icons */
.icons_filter_type .option {
  @apply py-3 cursor-pointer border-y-0 border w-[120px] border-x-clr_white_gray flex items-center justify-center text-xs font-bold uppercase;
}
.icons_filter_type .option span {
  @apply ml-2 block;
}
.icons_filter_type ul li.type_active {
  @apply bg-clr_blue_2 text-clr_white_1;
}
.icons_filter_type ul li.type_active img {
  @apply invert;
}

/* results grid */
.my_result_items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(115px, 1fr));
}
/* results grid */
.icons_results .icon,
.popup_result--icon .icon {
  @apply relative rounded-2xl pt-[100%] flex items-center justify-center bg-clr_white_1 cursor-pointer hover:bg-clr_gray;
}
.popup_result--icon .icon .icon_content,
.icons_results .icon .icon_content {
  @apply absolute top-[45%] left-1/2 m-auto -translate-x-1/2 -translate-y-1/2;
}
.icons_results .icon .icon_content p {
  @apply text-sm relative top-3;
}
.icons_results .icon .icon_content img {
  @apply w-10;
}

/* footer */
.social-media .footer--links img {
  @apply w-6 h-6;
}
.footer--top > div {
  @apply opacity-100;
}
.footer--top .footer--title {
  @apply mb-4 font-extrabold text-sm uppercase;
}

.footer--links > li {
  @apply flex;
}
.footer--links > li:hover a,
.footer--company_links li:hover a {
  @apply font-bold underline underline-offset-4;
}
.footer--links > li i {
  @apply shrink-0;
}
.footer--links > li a {
  @apply relative left-2;
}
